@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
html {
  background-color: #0d1117;
  color: white;
  --scrollbarBG: #0d1117;
  --thumbBG: #f0f0f0;
  height: 100vh;
  /* width: 100%; */
}

body {
  /* font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace; */
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

* {
  scrollbar-width: thin;
  scrollbar-color: #f0f0f0 #0d1117;
  scrollbar-color: var(--thumbBG) var(--scrollbarBG);
}

*::-webkit-scrollbar {
  width: 11px;
}

*::-webkit-scrollbar-track {
  border-radius: 5px;
  background: #0d1117;
  background: var(--scrollbarBG);
}

*::-webkit-scrollbar-thumb {
  background-color: #f0f0f0;
  background-color: var(--thumbBG);
  border-radius: 5px;
  border: 3px solid #0d1117;
  border: 3px solid var(--scrollbarBG);
}

#application {
  display: flex !important;
  width: 100%;
  height: 100%;
  padding-top: 50px;
  /* overflow: hidden; */
  min-height: 100vh;
  background-color: #06090f;
}

.main-container {
  flex: 8 1;
  padding: 20px 15px;
  /* overflow: hidden; */
}

.main-content {
  flex: 1 1;
  min-height: 100%;
  background-color: #06090f;
}

/*#region Logo & Loading */

.app-logo-container {
	display: flex;
	width: 100%;
}

.app-logo-container p {
	font-size: 14px;
	margin: 0;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	color: white;
}

.app-logo {
	height: 30px;
	pointer-events: none;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	transition-property: all;
	-webkit-transition-property: all;
	transition-duration: infinite;
	-webkit-transition-duration: infinite;
	animation: app-logo-spin infinite 10s linear;
	-webkit-animation: app-logo-spin infinite 10s linear;
}

.app-logo-loading {
	height: 90px;
	pointer-events: none;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	opacity: 1;
	transition-property: all;
	-webkit-transition-property: all;
	transition-duration: infinite;
	-webkit-transition-duration: infinite;
	animation: app-logo-spin infinite 10s linear;
	-webkit-animation: app-logo-spin infinite 1s ease-in-out;
}

.loading {
	position: absolute;
	height: 100%;
	width: 100%;
	opacity: 0.5;
	top: 0;
	left: 0;
	z-index: 50;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: black;
}

@keyframes app-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

/*#endregion*/

/*#region Buttons */

.button {
	display: flex !important;
	padding: 10px 20px !important;
	min-height: 35px !important;
	border-radius: 4px !important;
	line-height: normal !important;
	line-height: initial !important;
	background-color: #21262d !important;
	border: 1px solid #30363d !important;
	text-align: center !important;
	justify-content: center !important;
	align-items: center !important;
	transition: 0.15s all !important;
	color: white !important;
	cursor: pointer !important;
	-webkit-user-select: none !important;
	    -ms-user-select: none !important;
	        user-select: none !important;
}

.button:hover {
	border-color: #656270;
	background-color: #252a31;
	transition: 0.15s all;
	box-shadow: inset 0px 0px 3px 1px rgba(44, 44, 44, 0.8);
}

.new:hover {
